    
video {
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
  }
.video-container{
    position: relative;
    width: auto;
}
#play{
    cursor: pointer;
}
video::-webkit-media-controls-mute-button:parent {
    background-color: yellow !important;
}
video::-webkit-media-controls-timeline {
    display:block;
    background-color: transparent;
    width: 76%;
    position: absolute;
    right: 6.5rem;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
video::-webkit-media-controls-volume-slider::-webkit-media-slider-container, 
video::-webkit-media-controls-timeline::-webkit-media-slider-container {
	border: 0px;
	border-radius: 5px;
    cursor: pointer;  
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
video::-webkit-media-controls-volume-slider-container,
video::-webkit-media-controls-volume-slider,
video::-webkit-media-controls-overflow-button,
video::-webkit-media-controls-fullscreen-button,
video::-webkit-media-controls-play-button,
video::-webkit-media-controls-mute-button,
video::-webkit-media-controls-mute-button:hover,
::-webkit-media-slider-thumb 
{
    display:none;
    -webkit-appearance: none;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
video::-webkit-media-controls-current-time-display{
	font-family: 'Lato', sans-serif;
	font-size: 13px;
    font-weight: normal; 
    position: absolute;
    right: 5rem;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
video::-webkit-media-controls-time-remaining-display {
	font-family: 'Lato', sans-serif;
	font-size: 13px;
    font-weight: normal;  
    position: absolute;
    left: 1rem;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
.circle-light-rec{
    width: 30px;
    height: 30px;
    background-color: #fdfdfd6b;
    text-align: center;
    border-radius: 50%;
    position: absolute;
    bottom: 0.3rem;
    left: 0.3rem;
}
.circle-light-play{
    width: 30px;
    height: 30px;
    background-color: #fdfdfd6b;
    text-align: center;
    border-radius: 50%;
    position: absolute;
    bottom: 0.2rem;
    left: 2.3rem;
    padding-right: 0.3rem;
}
.rec{
    width:18px;
    height: 18px;
    border-radius: 50%;
    background-color: #dc3545;
    transition: 1s;
   
}
.stop{
    border-radius: 1px;
    width:15px;
    height: 15px;
    background-color: #dc3545;
    transition: 1s;
    left: 8px !important;
    top: 8px !important;
}
.btns-group{
    position: absolute;
    bottom: 0.9rem;
    left:0.4rem;
    transition: 2s;
  
}
.btn-group-rec-style{
    background-color: #04040482;
    width: 16rem;
    height: 2.5rem;
    border-radius: 24px;
    left: 33%; 
    transition: 2s; 
}
.btn-rec-position{
    position: absolute;
    left: 6px;
    top: 6px;
}
.btn-play-position{
    position: absolute;
    left: 0.3rem;
    top: 0.3rem;
}
.start-text{
    position: absolute;
    left: 2.2rem;
    top: 0.3rem;
}
.recording-text{
    position: absolute;
    left: 2.2rem;
    top: 0.3rem;
}
.play-text{
    position: absolute;
    left: 4.2rem;
    top: 0.3rem;
}
.time-limitation{
    position: absolute;
    left : 12rem;
    top: 0.3rem;   
}
.loading{
    position: fixed;
    z-index: 12345;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.274);
    text-align: center !important;
    top: 0px;
    left: 0px;
    padding-top: 15rem;
}

