.offset-6 {
    margin-left: 60.333333%;
}
.btn-dots::after{
    content: "";
    background-color: #f2f2f2 !important;
    border-radius: 50%;
    width:20px;
    height: 20px;
}
.card-container {
    border: none;
    width: 100%;
    margin: 0 !important;
    padding: 0;
}
.btn-dots:hover{
    background-color: #f2f2f2 !important;
    border-radius: 5px;
    padding: .2rem 0rem !important;
}
.success{
    position: absolute;
    right: 6px;
    top: 6px;
}
@media only screen and  (max-width:1024px){
    .offset-6 {
        margin-left: 50%;
    }
}
@media only screen and  (max-width:414px){
    .btn-group-rec-style {
        left: 15%;
    }
    .offset-6{
        margin-left: 34%;
    }
}
@media only screen and  (max-width:375px){
    .btn-group-rec-style {
        left: 9%;
    }
    .offset-6{
        margin-left: 29%;
    }
}
@media only screen and (max-width: 360px)
{
    .offset-6 {
        margin-left: 25%;
    }
}
@media only screen and  (max-width:320px){
    .btn-group-rec-style {
        left: 3%;
    }
    .offset-6{
        margin-left: 15%;
    }
}




